export const lyrics = trackId => {
  return fetch(
    base +
      "track.lyrics.get?" +
      encode({
        track_id: trackId
      })
  )
    .then(response => {
      return response.json();
    })
    .then(response => {
      console.log(response);
      return response.message.body.track_list;
    });
};
