<script>
  import SongResult from "./SongResult.svelte";
  import { createEventDispatcher } from "svelte";
  const dispatch = createEventDispatcher();

  export let q, qid, songs;
</script>

<label for="{qid}-skip">
  <input
    type="radio"
    value={-1}
    name="q{qid}"
    id="{qid}-skip"
    checked
    on:change={e => {
      if (e.target.checked) {
        dispatch('select', { track: -1, query_index: qid });
      }
    }} />
  Do not save
</label>
{#each songs as song, i}
  <SongResult {song} {qid} {i} on:select />
{:else}No results{/each}
